export class MessageHandler {
    bridgeEventName;
    client;
    constructor(args) {
        this.bridgeEventName = args.bridgeEventName;
        this.client = args.client;
        this.client.addEventListener(this.bridgeEventName, this.handle.bind(this));
    }
}
export class MonitorConnectedMessageHandler extends MessageHandler {
    constructor(args) {
        super({ bridgeEventName: "Monitor Connect", client: args.client });
    }
    handle(message) {
        this.client.log("%c ⬅️ Monitor Connect ", "color: BlueViolet; font-weight: bold; border: solid;", message);
    }
}
