export class NewItemPlayingMessageHandler extends MessageHandler {
    constructor(args) {
        super({ bridgeEventName: "New Item Playing", client: args.client });
    }
    handle(message) {
        this.client.log("%c ⬅️ New Item Playing ", "color: BlueViolet; font-weight: bold; border: solid;", message);
        let index = this.client.currentPlaylistIndex;
        let playlistName = this.client.playlists?.[index]?.name;
        let playlistItemIndex = this.client.currentPlaylistItemIndex;
        if (message.payload.value.playlist_name.value == playlistName &&
            message.payload.value.index.value == playlistItemIndex) {
            this.client.isCastPending = false;
        }
    }
}
export class AllEventsMessageHandler extends MessageHandler {
    constructor(args) {
        super({ bridgeEventName: "All Events", client: args.client });
    }
    handle(message) {
        if (message.payload.value.event.value === "Progress Update")
            return;
        this.client.log(`%c ⬅️ ${message.payload.value.event.value}`, "color: BlueViolet; font-weight: bold; border: solid;", message);
    }
}
